

























































































































import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";

@Component({
  components: {  },
  mixins: [mixin]
})
export default class addresslist extends Vue {
  addressList = [];
  // 删除地址信息
  deladdress="";
  deladdressid=""
  delshow=false;
  // 新增地址
  addshow=false;
  province=[];
  provinceid="";
  city=[];
  cityid="";
  country=[];
  countryid="";

  updateaddressid="";
  address="";
  contactName="";
  contactMobile="";
  isDefault=0;
  isdef(){
    if(this.isDefault==0){
      this.isDefault=1
    }else{
      this.isDefault=0
    }
  }

gbdelshow(){
  this.delshow=!this.delshow
}
  close(){
    this.addshow=false;
  }
  getAddressList() {
    Vue.prototype.$reqFormPost(
      "/address/list",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
           this["$Message"].warning(res.data.message);
          return;
        }
        console.log("地址列表", res.data.data.addressList);
        this.addressList = res.data.data.addressList;
        this.addressList.forEach((item, index) => {
          item["id"] = item.addressId;
          item["name"] = item.contactname;
          item["tel"] = item.contactmobile;
          item["readdress"] = item.address;
          item["isDefault"] = item.isdefault;
          // item["address"] =
          //   item.province + item.city + item.country + item.address;
        });
      }
    );
  }
  selectDefault(params) {
    Vue.prototype.$reqFormPost(
      "/address/setdefault",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        addressId: params.addressId
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
           this["$Message"].warning(res.data.message);
          return;
        }
         this["$Message"].success("设置默认地址成功");
        this.getAddressList();
      }
    );
  }
  del(address){
    this.deladdress=address;
    this.deladdressid=address.id;
    this.delshow=true;
  }
  delcancel(){
    this.deladdress="";
    this.deladdressid="";
    this.delshow=false;
  }
  update(address){
    this.updateaddressid=address.id;
    this.addshow=true;
    this.provinceid=address.provinceid;
    this.cityid=address.cityid;
    this.countryid=address.countryid;
    this.address=address.address;
    this.contactName=address.contactname;
    this.contactMobile=address.contactmobile;
    this.isDefault=address.isdefault;
    this.querycity();
    this.querycountry();
  }
  deleteaddress(){
     Vue.prototype.$reqFormPost(
      "/address/delete",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        addressId: this.deladdressid
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
           this["$Message"].warning(res.data.message);
          return;
        }
         this["$Message"].success("删除地址成功");
        console.log("删除地址", res.data.data);
        this.deladdress="";
        this.deladdressid="";
        this.delshow=false;
        this.getAddressList();
      }
    );
  }
  newaddress(){
    this.addshow=true;
  }
  addcancel(){
    this.addshow=false;
    this.provinceid="";
    this.cityid="";
    this.countryid="";
    this.address="";
    this.contactName="";
    this.contactMobile="";
    this.isDefault=0;    
    this.updateaddressid="";
  }
  getprovince(){
    Vue.prototype.$reqFormPost(
      "/address/queryprovince",
      {},
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
           this["$Message"].warning(res.data.message);
          return;
        }
        console.log(res.data.data.region)
        this.province=res.data.data.region
      }
    );
  }
  changeprovince(){
    this.cityid="";
    this.city=[];
    this.countryid="";
    this.country=[];
    this.querycity();
  }
  changecity(){
    this.countryid="";
    this.country=[];
    this.querycountry();
  }
  querycity(){
    Vue.prototype.$reqFormPost(
      "/address/querycity",
      {
        provinceId:this.provinceid
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
           this["$Message"].warning(res.data.message);
          return;
        }
        console.log(res.data.data.region)
        this.city=res.data.data.region
      }
    );
  }
  querycountry(){
    Vue.prototype.$reqFormPost(
      "/address/querycountry",
      {
        cityId:this.cityid
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
           this["$Message"].warning(res.data.message);
          return;
        }
        console.log(res.data.data.region)
        this.country=res.data.data.region
      }
    );
  }
  
  addaddress() {
    if(this.provinceid==""){
       this["$Message"].warning("请选择省份");
      return;
    }
    if(this.cityid==""){
       this["$Message"].warning("请选择城市");
      return;      
    }
    if(this.countryid==""){
       this["$Message"].warning("请选择市区");
      return;
    }
    if(this.address==""){
       this["$Message"].warning("请填写详细地址");
      return;
    }
    if(this.address.length<5){
       this["$Message"].warning("详细地址请至少输入五个字符");
      return;
    }
    if(this.contactName==""){
       this["$Message"].warning("请填写收货人");
      return;
    }
    if(this.contactMobile==""){
       this["$Message"].warning("请填写手机号码");
      return;
    }
       if(this.contactMobile.length!= 11 ){
       this["$Message"].warning("请填写正确的手机号码");
      return;
    }
    console.log('长度',this.address.length);
    if(this.updateaddressid){
      Vue.prototype.$reqFormPost(
      "/address/update",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        addressId:this.updateaddressid,
        provinceId:this.provinceid,
        cityId:this.cityid,
        countryId:this.countryid,
        address:this.address,
        contactName:this.contactName,
        contactMobile:this.contactMobile,
        isDefault:this.isDefault
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
 
             this["$Message"].warning(res.data.message);
          return;
        }
         this["$Message"].success("保存成功");
        this.getAddressList();
        this.provinceid="";
        this.cityid="";
        this.countryid="";
        this.address="";
        this.contactName="";
        this.contactMobile="";
        this.isDefault=0;
        this.addshow=false;
        this.updateaddressid="";
      }
    );
    }else{
      Vue.prototype.$reqFormPost(
      "/address/add",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        provinceId:this.provinceid,
        cityId:this.cityid,
        countryId:this.countryid,
        address:this.address,
        contactName:this.contactName,
        contactMobile:this.contactMobile,
        isDefault:this.isDefault
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
             this["$Message"].warning(res.data.message);
          return;
        }
        this["$Message"].success("保存成功");
        this.getAddressList();
        this.provinceid="";
        this.cityid="";
        this.countryid="";
        this.address="";
        this.contactName="";
        this.contactMobile="";
        this.isDefault=0;
        this.addshow=false;
        this.updateaddressid="";
      }
    );
    }
  }
  mounted() {
       this.$emit('selectMenu',{
          name: '地址管理',
          url:'/addresslist',
        })
    this.getAddressList();
    this.getprovince();
  }
}



